html {
  font-family: 'Helvetica Neue', 'Segoe UI', Arial, sans-serif;
}
h1 {
  line-height: 1.15;
  margin-top: 0;
  margin-bottom: 0.375em;
  font-weight: 600;
}
html {
  box-sizing: border-box;
}
*,
*:after,
*:before {
  box-sizing: inherit;
}
.input-group {
  margin-bottom: 1.5em;
  display: block;
}
label {
  font-weight: 500;
  display: block;
  line-height: 1.15;
}
